.feedback {
  .title {
    text-align: left;
  }

  .descr {
    text-align: left;
    max-width: 520px;
  }

  .primary-btn {
    margin-top: 24px;
  }

  &-wrapper {
    display: flex;
    gap: 120px;

  }

  &-content {}

  &-form {
    width: 640px;
    padding: 40px;
    border-radius: 32px;
    background: var(--gray-800, #1F1F1F);

    input {
      font-family: Inter;
      font-size: 16px;
      display: block;
      border-radius: 16px;
      background: var(--gray-900, #141414);
      color: #afafaf;
      padding: 24px;
      width: 100%;
      margin-bottom: 16px;

      &:nth-child(3) {
        
      }
    }
  }
}