.team {
  padding: 80px 0;

  .title {
    margin-bottom: 64px;
  }

  &-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 64px;
  }

  &-card {
    max-width: 350px;

    img {
      border-radius: 32px;
      background: var(--primary-white-100, #FFF);
      width: 350px;
      height: 240px;
      object-fit: cover;
      margin-bottom: 32px;
    }

    .team-descr {
      color: var(--gray-500, #757575);
      text-align: center;
      font-size: 14px;
      font-weight: 700;
      line-height: 114.286%;
      letter-spacing: 4px;
      text-transform: uppercase;
      margin-bottom: 16px;
    }

    .team-name {
      color: var(--primary-white-100, #FFF);
      display: block;
      text-align: center;
      font-size: 20px;
      font-weight: 600;
      line-height: 140%;
      letter-spacing: -0.4px;
    }
  }
}