.slick-arrow {
  background: var(--gray-900, #141414);
  position: absolute;
  z-index: 5;
}

.slick-prev,
.slick-next {
  position: absolute;
  top: 50%;

  display: block;

  width: 56px;
  height: 56px;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);

  cursor: pointer;

  color: transparent;
  border: none;
  border-radius: 17px;
  outline: none;

  transition: opacity 0.1s ease-in-out;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  opacity: .9;
}

.slick-prev:before,
.slick-next:before {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -75px;

  &::after {
    content: url(./img/slick/arrow-previous.svg);
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
  }
}

.slick-next {
  right: -75px;

  &::after {
    content: url(./img/slick/arrow-next.svg);
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
  }
}

.slick-prev,
.slick-next {
  width: 56px;
  height: 56px;
}




.comments {
  padding: 80px 0;

  &-wrapper {
    display: flex;
    gap: 40px;
  }

}

.comment {
  border-radius: 32px;
  background: var(--gray-900, #141414);
  display: flex;
  padding: 56px 40px;
  gap: 32px;
  width: 580px;



  &-img {
    border-radius: 50%;
    width: 64px;
    height: 64px;
    object-fit: cover;
  }

  &-text {
    color: var(--primary-white-100, #FFF);
    font-size: 18px;
    font-weight: 600;
    line-height: 133.333%;
    letter-spacing: -0.36px;
    margin: 0;
    margin-bottom: 30px;
  }

  &-name {
    color: var(--gray-500, #757575);
    font-size: 14px;
    font-weight: 700;
    line-height: 114.286%;
    letter-spacing: 4px;
    text-transform: uppercase;
  }
}