.footer {
  padding: 56px 0;

  &-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 24px;
    background: var(--gray-900, #141414);
    height: 105px;
    padding: 24px;

    .logo {
      flex: 1;
    }

    .copyright {
      flex: 1;
      color: var(--gray-500, #757575);
      text-align: center;
      font-size: 18px;
      font-weight: 500;
      line-height: 155.556%;
      letter-spacing: -0.36px;
    }

    .nav {
      flex: 1;
      &-list {
        display: flex;
        justify-content: end;
      }
      &-item {
        padding: 20px;
      }
    }
  }
}