.tariffs {
  padding: 80px 0;

  .title {
    margin-bottom: 32px;
  }

  .descr {
    margin: 0 auto;
    margin-bottom: 80px;
  }

  .primary-btn {
    width: 100%;
  }

  &-wrapper {
    display: flex;
    gap: 32px;
    justify-content: center;
  }

  &-card {
    border-radius: 40px;
    background: var(--gray-800, #1F1F1F);
    width: 350px;
    // height: 628px;
    padding: 40px;

    .descr {
      text-align: left;
      margin-bottom: 32px;
    }
  }

  &-title {
    color: var(--primary-white-100, #FFF);
    font-size: 24px;
    font-weight: 700;
    line-height: 133.333%;
    letter-spacing: -0.48px;
    margin-bottom: 32px;
  }

  &-price {
    color: var(--primary-white-100, #FFF);
    font-size: 44px;
    font-weight: 700;
    line-height: 118.182%;
    letter-spacing: -2.2px;
    margin-bottom: 46px;

    span {
      color: var(--gray-500, #757575);
      font-size: 18px;
      font-weight: 700;
      line-height: 133.333%;
      letter-spacing: -0.36px;
      margin-left: 8px;
      vertical-align: middle;
    }
  }
}

.feature {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 24px;

  &-img {
    width: 40px;
    height: 40px;
    border-radius: 8px;
    background: var(--gray-600, #545454);
  }

  &-name {
    color: var(--primary-white-100, #FFF);
    font-size: 16px;
    font-weight: 800;
    line-height: 150%;
    letter-spacing: -0.32px;
  }
}